.show-more {
  display: none;
  margin-bottom: 34px; }
  @media screen and (min-width: 768px) {
    .show-more {
      display: flex !important; } }

.show-more {
  color: #000;
  font-weight: bold;
  display: none;
  align-items: center; }

.show-more--show::before {
  content: url(./static/arrow-down.svg);
  margin-right: 9px; }

.show-more--close::before {
  content: url(./static/arrow-down.svg);
  margin-right: 9px;
  transform: rotate(180deg); }

.section-header {
